@font-face {
  font-family: "Panton";
  src: local("Panton"), url("../fonts/Panton.woff2") format("woff2"), url("../fonts/Panton.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PantonBold";
  src: local("PantonBold"), url("../fonts/PantonBold.woff2") format("woff2"), url("../fonts/PantonBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PantonExtraBold";
  src: local("PantonExtraBold"), url("../fonts/PantonExtraBold.woff2") format("woff2"), url("../fonts/PantonExtraBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PantonSemiBold";
  src: local("PantonSemiBold"), url("/source/fonts/PantonSemiBold.woff2") format("woff2"), url("../fonts/PantonSemiBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "PantonSemiBold", arial, sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 22px;
  color: #1d1e1e; }

main {
  flex-grow: 1; }

.content {
  flex: 1 0 auto; }

.overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

img {
  max-width: 100%;
  height: auto; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

span {
  display: block; }

button:active,
button:focus {
  outline: none; }

a:focus,
button:focus {
  outline: #102654 solid 1px; }

button::-moz-focus-inner {
  border: 0; }

/* Вверх шапки */
.header__top_wrap {
  min-height: 45px;
  background-color: #1d1e1e; }

.header__top {
  display: flex;
  max-width: 1170px;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  font-family: "PantonBold", arial, sans-serif;
  font-size: 14px;
  line-height: 25px;
  font-weight: 700;
  color: #ffffff;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.header__services {
  list-style: none;
  display: flex; }

.header__services li {
  position: relative;
  margin-right: 68px;
  letter-spacing: 0.009em; }
  .header__services li::after {
    position: absolute;
    content: "";
    width: 9px;
    height: 9px;
    border: 2px solid #6a6a6a;
    border-radius: 50%;
    top: 8px;
    right: -37px; }
  .header__services li:last-child::after {
    content: none; }

.header__phone {
  display: flex;
  font-size: 13px;
  letter-spacing: 0.02em; }

.header__phone a {
  font-size: 20px;
  color: #009ee2;
  margin-left: 31px;
  letter-spacing: 0.042em; }
  .header__phone a:hover {
    opacity: 0.5; }

/* Нжняя половина шапки */
.header__bottom {
  display: flex;
  max-width: 1170px;
  align-items: center;
  height: 122px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.header__logo {
  font-family: "PantonExtraBold", arial, sans-serif;
  font-size: 50px;
  line-height: 55px;
  font-weight: 900;
  color: #0086c0;
  margin-right: 27px;
  letter-spacing: 0.09em; }

.header__form {
  display: flex;
  max-width: 556px;
  width: 100%;
  height: 42px;
  border: 1px solid #dddddd;
  margin-right: auto; }

.header__form input {
  width: 100%;
  border: none; }

.header__form input::placeholder {
  font-family: "Panton", arial, sans-serif;
  font-size: 14px;
  line-height: 25px;
  font-weight: 400;
  padding-left: 23px;
  letter-spacing: 0.05em; }

.header__form_btn {
  width: 58px;
  height: 40px;
  background-image: url("../img/search-icon.png");
  background-repeat: no-repeat;
  background-position: 15px 10px; }
  .header__form_btn:hover {
    opacity: 0.5; }

.header__enter {
  width: 26px;
  height: 26px;
  background-image: url("../img/enter-icon.png");
  background-repeat: no-repeat;
  background-size: cover; }
  .header__enter:hover {
    opacity: 0.5; }

.header__favoriter {
  position: relative;
  width: 28px;
  height: 24px;
  background-image: url("../img/favoriter-icon.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 12px;
  margin-left: 69px; }
  .header__favoriter sup {
    position: absolute;
    font-family: "PantonSemiBold", arial, sans-serif;
    font-size: 16px;
    line-height: 25px;
    font-weight: 600;
    color: #009ee2;
    top: -10px;
    right: -11px; }
  .header__favoriter:hover {
    opacity: 0.5; }

.header__basket {
  position: relative;
  width: 28px;
  height: 26px;
  background-image: url("../img/basket-icon.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 21px;
  margin-left: 59px; }
  .header__basket sup {
    position: absolute;
    font-family: "PantonSemiBold", arial, sans-serif;
    font-size: 16px;
    line-height: 25px;
    font-weight: 600;
    color: #009ee2;
    top: -10px;
    right: -11px; }
  .header__basket:hover {
    opacity: 0.5; }
